import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
//
import { MatPaginator, MatSort } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { debounceTime } from 'rxjs/operators';
import { Subscription } from 'rxjs';
//
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
//
import { StylesService } from "../../services/styles.service";
import { Style, StylesListResponse } from "../../models/style";
import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';

const errorKey = 'Error';

@Component({
    selector: 'styles-table',
    templateUrl: './styles-table.component.html',
    styleUrls: ['./styles-table.component.scss']
})
export class StyleTableComponent implements OnInit {

    displayedColumns: string[] = [
        'name',
        'parent',
        'brand',
        //'collection',
        'category',
        'updatedAt',
        'actions'
    ];

    brands: Array<Brand>;

    categories: Array<Category>;

    filter: FormGroup;

    filterValueChanges: Subscription;

    @ViewChild(MatPaginator) paginator: MatPaginator;

    @ViewChild(MatSort) sort: MatSort;

    totalLength: number = 0;

    stylesList: Subscription;

    styles: Array<Style> = [];

    styless: Array<Style>

    constructor(
        public activatedRoute: ActivatedRoute,
        public stylesService: StylesService,
        public errorHandlingService: ErrorHandlingService
    ) {
    }

    ngOnInit() {
        this.filter = this.createFilterFormGroup();
        this.filterValueChanges = this.filter.valueChanges.pipe(debounceTime(500)).subscribe(change => this.onFilter());
        this.paginator.pageIndex = 0;

        this.brands = this.activatedRoute.snapshot.data.brands;
        this.categories = this.activatedRoute.snapshot.data.categories;
        this.styless = this.activatedRoute.snapshot.data.styles;

        // Begin observing style list changes.
        this.stylesList = this.stylesService.stylesList.subscribe((stylesList: any) => {
            this.totalLength = stylesList.dataCount;
            this.styles = stylesList.data;
            if (this.styles.length === 0 && this.totalLength > 0 && this.stylesService.previousPageSize > 0) {
                this.stylesService.previousPageIndex =
                    Math.ceil(this.totalLength / this.stylesService.previousPageSize) - 1;
                this.stylesService.reloadStyles().subscribe(response => {
                    this.stylesService.stylesList.next(response);
                },
                    (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error));
            }
        });
    }


    ngAfterViewInit() {
        this.loadPage();
    }

    ngOnDestroy() {
        this.stylesList.unsubscribe();
        this.filterValueChanges.unsubscribe();
    }

    createFilterFormGroup() {
        let group: any = {};
        group['name'] = new FormControl('');
        group['brand'] = new FormControl('');
        group['category'] = new FormControl('');
        return new FormGroup(group);
    }

    loadPage() {
        this.stylesService.getStyles(
            Object.assign({}, this.filter.value),
            this.sort.active, this.sort.direction,
            this.paginator.pageIndex, this.paginator.pageSize).subscribe((response: StylesListResponse) => {
                this.stylesService.stylesList.next(response);
            },
                (err: HandledError) => {
                    this.errorHandlingService.handleUiError(errorKey, err)
                });
    }

    onFilter() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onSort() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onPage() {
        this.loadPage();
    }

    getBrand(id: string) {
        try {
            return this.brands.find(brand => {
                return brand.id === id;
            }).name;
        }
        catch (err) {
            return;
        }

    }

    getCategory(id: string) {
        try {
            return this.categories.find(category => {
                return category.id === id;
            }).name;
        }
        catch (err) {
            return;
        }
    }

    getParent(id: string) {
        try {
            return this.styles.find(style => {
                return style.id === id;
            }).name;
        }
        catch (err) {
            return;
        }
    }

}

